<template>
  <div class="body">
    <wv-header title="世界杯猜胜负" @headerClick="onHeaderClick">
	  <div class="btn-back" slot="left">
		<span class="iconfont icon-back" @click="$router.push('/')">&lt;</span>
	  </div>
	</wv-header>
	<p class="infoBox">{{info.name}}，您的金币：<span style="color:red" v-text="info.coins">500</span>。</p>
	<div class="matchBox">
	  <header>往期竞猜</header>
	  <div v-if="match_list.length === 0">您还未参加任何竞猜</div>
	  <div class="matchDiv" v-for="(m, i) in match_list">
	    <p>{{m.type}}比赛：{{m.start_time}}</p>
		<wv-flex class="col-1">
		  <wv-flex-item flex="4">{{m.country_a_cn}}</wv-flex-item>
		  <wv-flex-item style="color:#fff;">VS</wv-flex-item>
		  <wv-flex-item flex="4">{{m.country_b_cn}}</wv-flex-item>
		</wv-flex>
		<div class="col-4 small-font" v-if="m.support_result === 'unknown'">比赛结果未结算</div>
		<div v-else class="col-4 small-font">您押{{m.pay_for}}金币，支持{{m.support_country_cn}}，赔率{{m.support_odds}}，让球{{m.handicap_num}}<br>
		90分钟比赛结果{{m.score_90min}}，{{m.support_country_cn}}<span v-if="m.support_result === 'win'">胜</span><span v-if="m.support_result === 'failed'">负</span><span v-if="m.support_result === 'draw'">平</span>，您<span v-if="!m.tf">亏</span><span v-if="m.tf">赚</span>{{m.pay_back}}金币</div>
	  </div>
	</div>
  </div>
</template>
<script>
import services from '@/http/services'
import { Toast } from 'we-vue'
export default {
  name: 'index',
  data() {
    return {
      match_list: [],
	  info: {
	    name: '',
		coins: 0
	  }
    }
  },
  methods:{
    getInfo: function () {
	  var self = this
	  services.httpApi.userinfo().then(function(resData){
        if (resData.code === 200) {
		  self.info.name = resData.data.user_name
		  self.info.coins = resData.data.coins
		} else {
		  Toast.fail(resData.msg)
		}
      });
	},
    getMatch: function () {
	  var self = this
	  services.httpApi.get_old_info().then(function(resData){
        if (resData.code === 200) {
		  self.match_list = []
		  var list = resData.data.schedules
		  list.forEach((e) => {
		    var o = Object.assign({}, e)
		    if(e.pay_back > 0) {
			  o.tf = true
			} else {
			  o.tf = false
			}
			o.pay_back = Math.abs(e.pay_back)
			self.match_list.push(o)
		  })
		} else {
		  Toast.fail(resData.msg)
		}
      });
	}
  },
  created () {
    Toast.loading('loading')
    this.getInfo()
	this.getMatch()
  }
}
</script>



